import React from 'react';
import styled from 'styled-components';
import CodeBlock from '../components/CodeBlock';

const Container = styled.div`
  max-width: 900px;
  margin: 0 auto;
`;

const Section = styled.section`
  margin-bottom: 3rem;
`;

const Title = styled.h1`
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: #0f172a;
`;

const Subtitle = styled.h2`
  font-size: 1.5rem;
  margin: 2rem 0 1rem;
  color: #0f172a;
  border-bottom: 1px solid #e2e8f0;
  padding-bottom: 0.5rem;
`;

const Text = styled.p`
  margin-bottom: 1rem;
  line-height: 1.6;
  color: #334155;
`;

const List = styled.ul`
  margin-bottom: 1.5rem;
  
  li {
    margin-bottom: 0.5rem;
    line-height: 1.6;
  }
`;

const InstallationPage: React.FC = () => {
  // 
  const libInstallCode = `go get github.com/scagogogo/golang-binary-dependencies-parser`;
  
  // CLI
  const cliInstallCode = `go install github.com/scagogogo/golang-binary-dependencies-parser/cmd/godeps@latest`;
  
  // 
  const libUsageCode = `package main

import (
  "fmt"
  "github.com/scagogogo/golang-binary-dependencies-parser/pkg/gobinaryparser"
)

func main() {
  // 
  info, err := gobinaryparser.ParseBinaryFromFile("/usr/local/bin/go")
  if err != nil {
    fmt.Printf(": %v\\n", err)
    return
  }
  
  // 
  fmt.Printf("Go: %s\\n", info.GoVersion)
  fmt.Printf(": %d\\n", len(info.Dependencies))
}`;

  // CLI
  const cliUsageCode = `# 
godeps /usr/local/bin/go

# 
godeps --nostdlib /usr/local/bin/go

# 
godeps find cobra /usr/local/bin/kubectl

# 
godeps stdlib /usr/local/bin/go`;

  return (
    <Container>
      <Section>
        <Title></Title>
        <Text>
          GoBinaryParser  Go 
          
        </Text>
      </Section>

      <Section>
        <Subtitle></Subtitle>
        <List>
          <li>Go 1.18 </li>
          <li> Go Modules </li>
        </List>
      </Section>

      <Section>
        <Subtitle></Subtitle>
        <Text>
           GoBinaryParser  Go 
        </Text>
        <CodeBlock 
          code={libInstallCode} 
          language="bash" 
          title="" 
          showLineNumbers={false}
        />
        <Text>
           Go  go.mod  go.sum 
        </Text>
      </Section>

      <Section>
        <Subtitle></Subtitle>
        <Text>
          
        </Text>
        <CodeBlock 
          code={cliInstallCode} 
          language="bash" 
          title=" CLI " 
          showLineNumbers={false}
        />
        <Text>
           GOPATH/bin  godeps  PATH 
           godeps 
        </Text>
      </Section>

      <Section>
        <Subtitle> - </Subtitle>
        <Text>
           GoBinaryParser 
        </Text>
        <CodeBlock 
          code={libUsageCode} 
          language="go" 
          title="" 
        />
      </Section>

      <Section>
        <Subtitle> - </Subtitle>
        <Text>
           Go 
        </Text>
        <CodeBlock 
          code={cliUsageCode} 
          language="bash" 
          title="CLI " 
        />
      </Section>

      <Section>
        <Subtitle></Subtitle>
        <Text>
           GoBinaryParser
        </Text>
        <List>
          <li><a href="/documentation"></a></li>
          <li><a href="/examples"></a></li>
          <li><a href="https://github.com/scagogogo/golang-binary-dependencies-parser" target="_blank" rel="noopener noreferrer">GitHub </a></li>
        </List>
      </Section>
    </Container>
  );
};

export default InstallationPage; 