  const basicCode = `package main

import (
  "fmt"
  "log"
  "os"
  "path/filepath"
  
  "github.com/scagogogo/golang-binary-dependencies-parser/pkg/gobinaryparser"
)

func main() {
  // 
  if len(os.Args) < 2 {
    fmt.Println(": go run main.go <Go>")
    fmt.Println(": go run main.go /usr/local/bin/go")
    os.Exit(1)
  }

  // 
  binaryPath, err := filepath.Abs(os.Args[1])
  if err != nil {
    log.Fatalf(": %v", err)
  }

  // 
  info, err := gobinaryparser.ParseBinaryFromFile(binaryPath)
  if err != nil {
    log.Fatalf(": %v", err)
  }

  // 
  fmt.Printf(" Go \\n\\n")
  fmt.Printf(": %s\\n", info.FilePath)
  fmt.Printf(": %s@%s\\n", info.Path, info.Version)
  fmt.Printf("Go: %s\\n", info.GoVersion)

  // 
  fmt.Printf("\\n: %d\\n", len(info.Dependencies))

  // 
  maxDeps := 10
  if len(info.Dependencies) < maxDeps {
    maxDeps = len(info.Dependencies)
  }

  fmt.Printf("\\n%d:\\n", maxDeps)
  for i := 0; i < maxDeps; i++ {
    dep := info.Dependencies[i]
    fmt.Printf("%d. %s@%s\\n", i+1, dep.Path, dep.Version)
    if dep.Replace != nil {
      fmt.Printf("   ( %s@%s)\\n", dep.Replace.Path, dep.Replace.Version)
    }
  }

  // 
  if len(info.Dependencies) > maxDeps {
    fmt.Printf("... %d \\n", len(info.Dependencies)-maxDeps)
  }
}`;

