  const findCode = `package main

import (
  "fmt"
  "log"
  "os"
  "path/filepath"
  "strings"

  "github.com/scagogogo/golang-binary-dependencies-parser/pkg/gobinaryparser"
)

func main() {
  // 
  if len(os.Args) < 3 {
    fmt.Println(": go run main.go <> <Go>")
    fmt.Println(": go run main.go cobra /usr/local/bin/go")
    os.Exit(1)
  }

  searchTerm := os.Args[1]
  binaryPath, err := filepath.Abs(os.Args[2])
  if err != nil {
    log.Fatalf(": %v", err)
  }

  // 
  info, err := gobinaryparser.ParseBinaryFromFile(binaryPath)
  if err != nil {
    log.Fatalf(": %v", err)
  }

  // 
  fmt.Printf(" \\n\\n")
  fmt.Printf(": %s\\n", info.FilePath)
  fmt.Printf("Go: %s\\n", info.GoVersion)
  fmt.Printf(": %d\\n", len(info.Dependencies))

  // 1:  - GetDependencyByPath
  fmt.Printf("\\n1:  \\"%s\\"\\n", searchTerm)
  dep := info.GetDependencyByPath(searchTerm)
  if dep != nil {
    fmt.Printf(" : %s@%s\\n", dep.Path, dep.Version)
    if dep.Replace != nil {
      fmt.Printf("   : %s@%s\\n", dep.Replace.Path, dep.Replace.Version)
    }
    if dep.Sum != "" {
      fmt.Printf("   : %s\\n", dep.Sum)
    }
  } else {
    fmt.Printf("  \\"%s\\" \\n", searchTerm)
  }

  // 2: 
  fmt.Printf("\\n2:  \\"%s\\" \\n", searchTerm)
  matches := info.FilterDependencies(func(dep gobinaryparser.DependencyInfo) bool {
    return strings.Contains(dep.Path, searchTerm)
  })

  if len(matches) > 0 {
    fmt.Printf("  %d :\\n", len(matches))
    for i, dep := range matches {
      fmt.Printf("%d. %s@%s\\n", i+1, dep.Path, dep.Version)
      if dep.Replace != nil {
        fmt.Printf("   : %s@%s\\n", dep.Replace.Path, dep.Replace.Version)
      }
    }
  } else {
    fmt.Printf("  \\"%s\\" \\n", searchTerm)
  }
}`;

