  const remoteCode = `package main

import (
  "context"
  "encoding/json"
  "fmt"
  "os"
  "time"

  "github.com/scagogogo/golang-binary-dependencies-parser/pkg/gobinaryparser"
)

func main() {
  // URL
  if len(os.Args) < 2 {
    fmt.Println(": go run main.go <GoURL>")
    fmt.Println(": go run main.go https://example.com/path/to/binary")
    os.Exit(1)
  }

  url := os.Args[1]
  fmt.Printf(" Go\\n\\n")
  fmt.Printf("URL: %s\\n", url)

  // 
  ctx, cancel := context.WithTimeout(context.Background(), 30*time.Second)
  defer cancel()

  // URL
  fmt.Println("\\n...")
  info, err := gobinaryparser.ParseBinaryFromURLWithContext(ctx, url)
  if err != nil {
    fmt.Printf(" : %v\\n", err)
    os.Exit(1)
  }

  // 
  fmt.Printf("\\n \\n")
  fmt.Printf(": %s@%s\\n", info.Path, info.Version)
  fmt.Printf("Go: %s\\n", info.GoVersion)
  fmt.Printf(": %d\\n", len(info.Dependencies))

  // JSON
  fmt.Println("\\nJSON (5):")

  type OutputInfo struct {
    Path         string                          \`json:"path"\`
    Version      string                          \`json:"version"\`
    GoVersion    string                          \`json:"go_version"\`
    Dependencies []gobinaryparser.DependencyInfo \`json:"dependencies"\`
  }

  output := OutputInfo{
    Path:         info.Path,
    Version:      info.Version,
    GoVersion:    info.GoVersion,
    Dependencies: info.Dependencies,
  }

  // 
  if len(output.Dependencies) > 5 {
    output.Dependencies = output.Dependencies[:5]
  }

  jsonData, _ := json.MarshalIndent(output, "", "  ")
  fmt.Println(string(jsonData))

  if len(info.Dependencies) > 5 {
    fmt.Printf("\\n...5%d\\n", len(info.Dependencies))
  }
}`;

